// Themes

.theme-primary {
  background-color: $color-lighter !important;
  h1,h2,h3,h4,h5,h6,p {
    color: $color-darker;
  }
}

.theme-white {
  background-color: white;
  h1,h2,h3,h4,h5,h6,p {
    color: $color-darker !important;
  }
}

.theme-inverse {
  background-color: $color-darker !important;
  h1,h2,h3,h4,h5,h6,p {
    color: $color-lighter;
  }
  a {
    color: $color-lighter;
    &:hover,
    &:focus,
    &:active,
    &.active {
      color: $color-light;
    }
    &.visited {
      color: $color;
    }
  }
}



